.top-banner
	@screen lg
		padding: calc(  var(--header-height) + 100px  ) 0 r(200px)
		min-height: r(812px)
	.img-bg
		@apply w-full h-full 
		&::after
			content: ''
			@apply block absolute top-0 left-0 pointer-events-none z-40  transition w-full h-full
			background: linear-gradient(270deg, rgba(226, 248, 255, 0) 43.54%, rgba(226, 248, 255, 0.362148) 49.64%, rgba(226, 248, 255, 0.666479) 56.58%, rgba(226, 248, 255, 0.860435) 74.95%, #DFF8FF 92.21%)
		a
			@apply w-full h-full
			img
				@apply w-full h-full
			@screen max-lg
				+img-ratio(960,1120)
	.heading-4
		background: linear-gradient(90deg, rgba(0, 179, 92, 0.2) 0%, rgba(0, 179, 92, 0) 100%)
	.row
		@media screen and ( max-width: 1024px)
			@apply mb-0
			.col
				@apply m-0
	.heading-1-up
		@apply text-6xl leading-130
		@screen xl
			@apply text-7xl leading-125