
.intro
	@apply fixed top-0 left-0 right-0 bottom-0 w-full h-full  pointer-events-auto flex-center 
	z-index: 999999 !important
	@media screen and ( max-width: 1200px)
		display: none !important
	.image-svg
		@apply max-w-[90%] sm:max-w-[50%]
	img
		@apply opacity-0
	.svg-generate
		@apply h-[200px]
		svg
			@apply h-full w-full 
		@media screen and ( max-width: 1024px)
			@apply h-[150px]