.home-news
	@apply bg-secondary-bg
	.title-wrap
		.ModuleContent,.Module
			@apply flex-between w-full
.news-list
	@apply bg-white !#{important}
	.news-big
		.img
			a
				+img-ratio(519,920)
		.txt
			@apply justify-center
			height: -webkit-fill-available
		.title
			a
				@apply text-2xl
		.desc
			@apply text-base
.news-big
	@apply h-full  block
	align-items: flex-start !important
	@screen md
		@apply flex-start 
	.img
		@apply w-full
		@screen md
			@apply h-full
			width: 66%
			flex: 1 0 auto
		a
			@screen max-lg
				+img-ratio(369,656)
			@screen lg
				@apply flex-center h-full
				width: 100%
				img
					width: 100%
					height: 100%
					object-fit: cover
	.txt
		@apply my-auto
		@screen max-md
			@apply h-fit
		@screen md
			width:  34%
			flex: 0 1 auto
	.desc
		@screen max-sm
			font-size: 14px
			*
				font-size: 14px

.side-news
	@apply transition  flex-start 
	align-items: flex-start !important
	.img
		@apply  w-full relative
		flex: 1 0 auto
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-40  transition w-full h-full
			max-height: r(130px)
			background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%)
		a
			@apply overflow-hidden  flex-center  relative z-30 w-full
			height: r(220px)
			img
				@apply w-full h-full
				object-fit: cover
	.txt
		flex: 0 1 auto
		@apply w-full
	.heading-5
		a
			@apply line-clamp-3
.time-wrap
	.type
		&::before
			content: ''
			@apply block absolute-y left-[-10px] pointer-events-none z-10  transition w-[2px] h-[2px] min-w-[2px] rounded-full overflow-hidden bg-grey-500
	time
		flex: 0 1 auto
		em,span
			@apply text-base
.side-figure
	@apply transition  flex-start 
	align-items: flex-start !important
	&:not(:last-child)
		@apply mb-10
	.img
		height: r(78px)
		width: r(106px)
		min-width: r(106px)
		max-width: r(106px)
		flex: 1 0 auto
		@screen max-sm
			height: r(120px)
			width: r(150px)
			min-width: r(150px)
			max-width: r(150px)
		a
			@apply overflow-hidden  flex-center rounded-2 w-full h-full
			img
				width: 100%
				height: 100%
				object-fit: cover
	.txt
		@apply w-full pr-5
		flex: 0 1 auto
.news-item,.news-figure
	.title
		+line-clamp(20px,24px,1.35,2)
	.desc
		+line-clamp(15px,16px,1.4,2)
.culture-5
	background: linear-gradient(180deg, #FFF 0%, #CEE3FF 100%)
	.box-wrap
		
	.mobile-text
		@apply block
		@screen md
			@apply hidden !#{important}
	.desktop-text
		@apply hidden !#{important}
		@screen md
			@apply block !#{important}
	.init-swiper
		@screen max-sm
			--spv: 1
		@screen md
			--spv: 3
		@media screen and ( min-width: 1024px)
			--spv: 3
		@media screen and ( min-width: 1200px)
			--spv: 4
	.txt
		@apply h-full flex flex-col justify-between relative
		@screen max-md
			@apply p-4 bg-primary-1 rounded-4
		@screen md
			max-height: r(300px)
			flex: 0 1  auto
			@apply p-0
		&::after
			content: ''
			@apply  absolute pointer-events-none left-1 bottom-0 z-10  transition w-full h-full bg-primary-1 rounded-2 hidden
			@screen md
				@apply block
				min-width: r(320px)
			@screen lg
				min-width: r(360px)
			@media screen and ( min-width: 1280px)
				min-width: r(210px)
		&::before
			content: ''
			@apply block absolute pointer-events-none  bottom-0 z-20  transition w-full h-full 
			right: r(-125px)
			width: r(201px)
			height: r(185px)
			background: url('../img/overlay/culture-2.png')
			background-repeat: no-repeat
			background-size: contain
			background-position: bottom right
	.img
		@screen max-md
			img
				width: auto
				height: 100%
				object-fit: contain
				max-height: r(400px)
				@apply mx-auto
	img
		@screen md
			shape-outside: var(--data-url)
			shape-margin: 1rem
			float: right
			flex: 0 0 auto
			width: r(205px)
		
			@apply object-contain
	.desc
		@apply text-sm line-clamp-3
		*
			@apply text-sm line-clamp-3
	.headline
		@screen max-md
			@apply my-3
		@screen md
			@apply pl-4 pt-4
			width: r(120px)
	.scrollbar-wrap
		max-height: r(100px)
		@screen md
			@apply pl-4  pb-4
		@screen max-sm
			max-height: 100%
	.caption
		@apply text-base line-clamp-3
		*
			@apply text-base line-clamp-3
		@screen max-sm
			@apply  text-left

	.prev,.next
		@apply border-white  bg-dark bg-opacity-20
		&::before
			@apply text-white