.culture-1
	+right-icon(212px,210px,'../img/overlay/culture-1.png',unset,0,0)
	.row
		@apply m-0 p-0
	.col
		@apply m-0 p-0
		@screen xl
			&:nth-child(1)
				width: 33.33%
			&:nth-child(2)
				width: 66.67%
	.block-wrap
		@apply py-15 px-4 
		@screen xl
			padding: calc( var(--header-height) + 2.5rem  ) 0 r(40px) r(60px)
	.img
		@apply w-full h-full
		a
			display: flex
			@apply w-full h-full
			img
				@apply w-full h-full
				object-fit: cover
			@screen max-xl
				+img-ratio(1,1)
	.btn-tertiary
		&:hover
			span,em,i
				@apply text-primary-1
		span,em,i
			@apply text-grey-500
	.title
		+line-clamp(20px,24px,1.35,2)
	.desc
		+line-clamp(15px,16px,1.4,2)
.culture-3
.news-figure-big
	@apply h-full  
	align-items: flex-start !important
	@screen max-md
		@apply flex flex-col-reverse
	@screen md
		@apply flex-start 
	.img
		@apply w-full
		@screen md
			@apply h-full
			width: 47.77%
			flex: 1 0 auto
		a
			+img-ratio(1,1)
	.txt
		@screen max-md
			@apply h-fit
		@screen md
			width:  52.23%
			flex: 0 1 auto
