.field-list
	background: linear-gradient(180deg, #FFFFFF 0%, #CEE3FF 100%)
	+right-icon(523px,844px,'../img/overlay/field-1.svg',unset,0,0)
	@screen md
		padding-bottom: r(160px)
	.item 
		@screen md
			width: r(380px)
			height: r(380px)
			position: relative
			display: inline-block
			margin-bottom: r(-68px)
			&::before 
				content: ""
				position: absolute
				top: 0
				bottom: 0 
				left: 0 
				right: 0
				clip-path: polygon(50% 0, 93.3% 25%, 93.3% 75%, 50% 100%, 6.7% 75%, 6.7% 25%)
				transform: scale(1.077)
				transition: transform 0.3s
		&:hover
			a
				&::before
					@apply opacity-0
				&::after
					@apply opacity-100
			.txt-grid
				grid-template-rows: 1fr
		a 
			&::before,&::after
				content: ''
				@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
			&::before
				background: linear-gradient(180deg, rgba(16, 72, 145, 0) 0%, rgba(16, 72, 145, 0.8) 100%)
			@screen md
				position: absolute
				top: 0 
				bottom: 0 
				left: 0 
				right: 0
				background-size: cover
				background-position: center
				clip-path: polygon(50% 0, 93.3% 25%, 93.3% 75%, 50% 100%, 6.7% 75%, 6.7% 25%)
			
				&::after
					@apply bg-primary-1 bg-opacity-80 opacity-0
			img
				@apply w-full h-full object-cover
	.title
		@apply  text-white font-bold  text-xl
		@screen xl
			@apply text-28
.honeycomb
	display: grid
	@screen max-md
		grid-template-columns: repeat(3, r(160px))
		.item:nth-child(3n+1) 
			grid-column: 1
		.item:nth-child(3n+2) 
			grid-column: 3
		.item:nth-child(3n+0) 
			grid-column: 2
	@screen md
		grid-template-columns: repeat(6, r(190px))
		.item:nth-child(5n+1) 
			grid-column: 1
		.item:nth-child(5n+2) 
			grid-column: 3
		.item:nth-child(5n+3) 
			grid-column: 5
		.item:nth-child(5n+4) 
			grid-column: 2
		.item:nth-child(5n+0) 
			grid-column: 4
.field-block-list
	@screen max-lg
		@apply pb-20
		background: linear-gradient(145deg, #C8E0FF 2.47%, #D3E6FF 100%)
	.row
		@apply m-0 p-0
		.col
			@apply m-0 p-0
	.container-fluid
		@screen lg
			@apply p-0
		.col
			@apply relative
	.container
		@screen max-lg
			@apply max-w-full
	.txt
		@screen lg
			padding: r(150px)  r(80px)
	.scrollbar-wrap
		max-height: r(120px)
	.img
		@screen lg
			@apply pl-8
	.block-wrap
		background: linear-gradient(145deg, #C8E0FF 2.47%, #D3E6FF 100%)
		@screen max-lg
			@apply flex flex-col-reverse 
			&:not(:last-child)
				@apply mb-15
		&:nth-child(odd)
			.container-fluid
				.col
					&:first-child
						@screen lg
							+left-icon(670px,811px,'../img/overlay/field-2.png',unset,0,0)
		&:nth-child(even)
			.container-fluid
				.col
					&:first-child
						@screen lg
							+right-icon(670px,811px,'../img/overlay/field-2.png',unset,0,0)
			.bg-wrap
				@screen lg
					@apply pr-0 pl-20
			.img
				@screen lg
					@apply pr-8 pl-0
			.row
				@apply flex-row-reverse
	.heading-1-up
		@apply font-extrabold
		*
			@apply font-extrabold