footer
	.footer
		padding: r(85px) 0
		@screen max-xl
			padding: r(85px) 0 r(40px)
.footer
	background: url('../img/bg/footer.jpg'),linear-gradient(180deg, #1259B4 0%, #022A5D 100%)
	background-repeat: no-repeat
	background-size: cover
	background-position: center
	@apply pb-5
	+right-icon(376px,482px,'../img/overlay/footer.png',unset,0,0)
	.heading-1-up
		@media screen and ( min-width: 1024px) and ( max-width: 1400px)
			@apply mb-5
	.img-wrap
		@screen max-lg
			@apply hidden
		a
			@apply flex-center
			height: r(890px)
			width: auto
			img
				width: auto
				height: 100%
				object-fit: contain
	.network
		li
			@media screen and ( min-width: 1024px) and ( max-width: 1400px)
				@apply mb-4
				.text,.title
					@apply text-xs
			@screen max-sm
				@apply mb-8
				
		.title
			em
				@apply text-base mr-2
				@media screen and ( min-width: 1024px) and ( max-width: 1400px)
					@apply text-xs
		a
			&:last-child
				@apply mb-0
			&:hover
				@apply underline
		p
			@apply mb-2
			&:last-child
				@apply mb-0
	address
		@screen 2xl
			@apply pr-5
		p
			@apply mb-0
		li
			@apply flex-start  text-sm leading-140
			align-items: flex-start
			&:not(:last-child)
				@apply mb-3
			*
				@apply text-sm
		.label
			@media screen and ( min-width: 1024px) and ( max-width: 1400px)
				@apply text-xs
				*
					@apply text-xs
		.txt
			@apply font-bold
			*
				@apply font-bold
			@media screen and ( min-width: 1024px) and ( max-width: 1400px)
				@apply text-xs
				*
					@apply text-xs
		a
			&:hover
				@apply underline
	.copyright
		@media screen and ( min-width: 1024px) and ( max-width: 1400px)
			@apply text-xs
	.social
		@media screen and ( min-width: 1024px) and ( max-width: 1400px)
			@apply py-4
		@screen max-sm
			@apply pt-10
		.ModuleContent
			@apply  flex-center gap-2 
			@screen xl
				@apply  flex-start
		em,i 
			@apply text-primary-1 text-xl
			@media screen and ( min-width: 1024px) and ( max-width: 1400px)
				@apply text-lg
		img
			@apply max-w-5
		a
			@apply bg-white flex-center transition overflow-hidden rounded-full w-[40px] h-[40px] min-w-[40px]
			@media screen and ( min-width: 1024px) and ( max-width: 1400px)
				@apply  w-[32px] h-[32px] min-w-[32px]
			@screen 2xl
				@apply  min-w-10 w-10 h-10
			&:hover
				transform: translateY(-4px)
	.footer-menu
		li
			&:hover,&.active
				a
					@apply underline
			&:not(:last-child)
				@apply mb-3

.footer-middle
	.heading-4
		@media screen and ( min-width: 1600px)
			max-width: r(310px)
	.row
		.col
			@screen max-xl
				&:nth-child(1)
					@apply order-4
				&:nth-child(4)
					@apply order-3

				&:nth-child()
					
			@screen xl
				&:nth-child(1),&:nth-child(3)
					@apply w-1/4
				&:nth-child(2)
					width: 17.5%
				&:nth-child(4)
					width: 32.5%
			&:nth-child(1)
				.heading-4
					@media screen and ( min-width: 1200px) and ( max-width: 1400px)
						@apply text-lg