.ability-1
	&::after
		content: ''
		@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
		background: linear-gradient(270deg, rgba(255, 255, 255, 0) 29.17%, rgba(255, 255, 255, 0.2) 53.24%, rgba(255, 255, 255, 0.85385) 72.85%, #FFFFFF 100%)
	@screen xl
		padding: calc(  var(--header-height) + 30px  ) 0 0 0
	.img-bg
		@apply w-full h-full
		a
			@apply w-full h-full
			img
				@apply w-full h-full
			@screen max-lg
				+img-ratio(812,1920)
	.txt-wrap
		margin: r(130px) 0
	.container,.block-wrap
		@apply relative z-50
	.item
		@apply transition
		&:last-child
			@apply border-transparent
		&:hover
			@apply bg-primary-1
	.block-wrap
		background: rgba(0, 28, 65, 0.50)
		backdrop-filter: blur(2px)

.ability-2
	padding-bottom: r(300px)
	@screen max-xl
		background-position: right center !important
	@screen xl
		padding-bottom: r(470px)
	.item
		@apply flex flex-col justify-start
.ability-3
	@apply relative
	&::after
		content: ''
		@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
		background: linear-gradient(0deg, rgba(0, 0, 0, 0.50) 0%, rgba(0, 0, 0, 0.50) 100%)
	.container
		@apply relative z-60
	.tab-nav
		li
			&:hover,&.active
				a
					@apply text-opacity-100 border-opacity-100
					text-shadow: 1px 0 0 #fff
	.swiper-nav
		@screen lg
			@apply justify-start !#{important}

	.prev
		@apply mr-6 !#{important}
	.item
		@apply h-full
		.img
			@apply  w-full relative
			flex: 0 1 auto
			@screen md
				width: 57.7%
		.txt
			flex: 1 1 auto
			@screen md
				width: 42.3%
	table
		@apply w-full
		td
			@apply py-3 text-lg border-t border-white border-opacity-20 text-white
			&:first-child
				@apply text-left
			&:last-child
				@apply text-right font-bold
	.scrollbar-wrap
		max-height: r(240px)
		@screen max-sm
			max-height: 100%
.ability-4
	@screen xl
		min-height: r(640px)
	.row
		@apply m-0 p-0
		.col
			@apply m-0 p-0
	.tab-item
		@apply h-full
	.img
		@apply w-full h-full
		a
			@apply w-full h-full
			img
				@apply w-full h-full object-cover

	.container-fluid
		@apply p-0
	.toggle-item
		transition: .3s all  ease-in-out 
		@apply pointer-events-auto relative
		list-style: none
		.wrap
			@apply cursor-pointer
		.title
			@apply transition  
			align-items: flex-start
		em,i
			@apply text-2xl transition
		.article
			@apply hidden 
		&.is-toggle
			@apply pb-4
			.title
				@apply  text-primary-1  border-primary-1 border-opacity-100
				*
					@apply  text-primary-1
				em,i
					@apply font-light
					transform: rotateX(180deg)
		&.is-toggle
			.article
				@apply block 
		.desc 
			a
				&:hover
					@apply underline text-primary-1
			table
				@apply w-full my-2
				tr
					td
						@apply border border-grey-100  text-left px-4 py-2
						&:first-child
							@apply font-bold text-base  
				
				p
					@apply mb-0
	.scrollbar-wrap
		max-height: r(140px)
		@screen max-sm
			max-height: 100%
	.block-wrapper
		@screen max-lg
			@apply  px-10
		@screen lg
			max-width: r(780px)
			padding: r(80px) 0  r(80px) r(64px)
		@screen xl
			max-width: r(790px)
			padding: r(80px) 0  r(80px) r(64px)
		@screen 2xl
			max-width: r(660px)
.ability-5
	background-repeat: no-repeat !important
	background-position:  bottom center !important
	@apply relative z-20
	@screen xl
		padding: r(130px) 0  r(264px) !important
	.tab-nav
		@apply w-fit
		@screen max-md
			@apply mx-auto
		li
			&.active,&:hover
				a
					@apply bg-primary-2 text-white
	.item
		@apply h-full
		@screen md
			@apply  flex
		.img
			@apply  w-full relative
			flex: 0 1 auto
			@screen md
				@apply w-1/2
		.txt
			flex: 1 1 auto
			@screen max-md
				@apply mt-5
			@screen md
				@apply w-1/2
	.logo
		height: r(90px)
		@apply px-2 w-fit
		img
			width: auto
			height: 100%
			object-fit: contain
	.scrollbar-wrap
		@apply pr-2 
		@screen max-sm
			max-height: 100%
		@screen sm
			max-height: r(360px)
		@screen md
			padding: 0 4px 0 r(40px) !important
		@screen lg
			max-height: r(490px)
		@screen 2xl
			max-height: r(430px)
	.list
		li
			@apply items-start 
			&:not(:last-child)
				@apply mb-4