.about-1
	@apply relative
	@screen lg
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
			background: linear-gradient(90deg,rgba(255, 255, 255, 1) 43%, rgba(255, 255, 255, 0.35) 60%, rgba(255, 255, 255, 0) 75%)
	@screen max-xl
		@apply pt-0 !#{important}
	@screen xl
		padding: calc(  var(--header-height) + 30px  ) 0 r(200px)
	.img-bg
		@apply w-full h-full
		@screen lg
			max-width: r(1120px)
		a
			@apply w-full h-full
			img
				@apply w-full h-full
			@screen max-lg
				+img-ratio(960,1120)
	.heading-4
		background: linear-gradient(90deg, rgba(0, 179, 92, 0.2) 0%, rgba(0, 179, 92, 0) 100%)
	.row
		@media screen and ( max-width: 1024px)
			@apply mb-0
			.col
				@apply m-0
	
	.container
		@apply relative z-60
.about-2
	background: linear-gradient(180deg, #F2F7FF 0%, #CEE3FF 100%)
	.container
		&::before
			content: ''
			@apply block absolute pointer-events-none z-100 transition w-full h-full right-full bottom-8
			background: url('../img/icon/title-1.png')
			background-repeat: no-repeat
			background-size: contain
			background-position: center
			width: r(136px)
			max-height: r(720px)
		&::after
			content: ''
			@apply block absolute pointer-events-none left-8 bottom-0 z-10  transition w-full h-full
			background: url('../img/overlay/about-1.svg')
			background-repeat: no-repeat
			background-size: contain
			background-position: center
			max-width: r(780px)
			
	.img
		@apply flex items-end 
		img
			@apply object-contain h-full
			@media screen and ( max-width: 1400px)
				@apply h-auto
			@screen max-xl
				max-height: r(400px)
		@screen max-xl
			@apply flex justify-center items-end
	.quotes-txt
		&::before
			content: ''
			@apply block absolute pointer-events-none z-100 left-10 transition w-full h-full
			background: url('../img/icon/open-quote.png')
			background-repeat: no-repeat
			background-size: contain
			background-position: center
			width: r(52px)
			height: r(42px)
			top: r(-16px)
	.info
		&::before
			content: ''
			@apply block absolute pointer-events-none z-100 right-10 transition w-full h-full
			background: url('../img/icon/close-quote.png')
			background-repeat: no-repeat
			background-size: contain
			background-position: center
			width: r(52px)
			height: r(42px)
			top: r(-24px)
	.name
		font-family: '1FTV VIP Alishanty Signature'
	.bg-wrap
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full bg-white bg-opacity-80
			backdrop-filter: blur(25.29999923706055px)
.about-3
	.history-swiper 
		&::after
			content: ''
			@apply block absolute-center pointer-events-none  z-10  transition w-full h-[1px] bg-grey-100
			@screen max-lg
				@apply hidden
		.swiper
			@apply relative z-40
	.txt
		@screen lg
			@apply w-full h-full
			flex: 1 1 auto
	.img
		@screen lg
			@apply  w-full  h-full
			flex: 1 1 auto
		
		a
			+img-ratio(160,320)
	.swiper-slide
		@apply h-auto
		.desc 
			@apply transition
			@media screen and ( max-width: 1024px)
				@apply opacity-0 mb-5
			strong
				@apply font-bold  text-primary-1
		&.swiper-slide-active
			.desc 
				@apply opacity-100
			.year
				@apply text-primary-2
			.item
				&::after
					width: r(36px)
					height: r(36px)
					@apply border-transparent
					background: url('../img/icon/dot.png')
					background-repeat: no-repeat
					background-size: cover
					background-position: center
					
	.item
		@apply flex flex-col justify-between relative h-full gap-x-0
		min-height: r(400px)
		@screen lg
			gap: r(80px) 
			min-height: r(600px)
		@media screen and ( min-width: 1400px)
			min-height: r(600px)
			gap: r(65px)
	
		@screen 2xl
			min-height: r(400px) 
		&::after
			content: ''
			@apply absolute-y left-[1px] block  pointer-events-none  z-30  w-4 h-4 min-w-4 transition  bg-grey-300 rounded-full 
			@media screen and ( max-width: 1024px)
				@apply hidden
	.year
		@apply font-bold mb-4
	.desc
		@apply line-clamp-4
	.swiper-slide
		&:nth-child(odd)
			.txt
				@screen lg
					@apply col-left
					justify-content: flex-end
					.year
						@apply order-2 pt-4 mb-0
					.desc
						@apply order-1
		&:nth-child(even)
			@screen lg
				.img
					@apply order-1 flex items-end
					a
						@apply w-full
				.txt
					@apply order-2  pb-0
	.swiper-nav
		@screen max-lg
			@apply mt-10
.about-4
	.item
		box-shadow: 0 4px 4px 0 rgba(31, 34, 39, 0.08)
	.btn.btn-tertiary
		span,em,i
			@apply text-white
	.swiper-slide
		&:nth-child(1)
			.item
				@apply bg-secondary-bg
			.btn.btn-tertiary
				span,em,i
					@apply text-grey-500
		&:nth-child(2),&:nth-child(3)
			.heading-2,.desc
				@apply text-white
		&:nth-child(2)
			.item
				@apply bg-primary-1
		&:nth-child(3)
			.item
				@apply bg-primary-2
.about-6
	.prev,.next
		@apply border border-white bg-dark bg-opacity-20
		&:before
			@apply text-white
	.prev,.next
		@screen max-lg
			@apply absolute-y
	.prev
		@screen max-lg
			@apply -left-8
	.next
		@screen max-lg
			@apply -right-8
.profile-item
	.img

		@apply relative
		&::before,&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
			background-repeat: no-repeat !important
			
		&::before
			background: url('../img/icon/line-1.png')
			background-size: contain !important
			background-position: center left !important
			height: r(230px)
		&::after
			@apply z-60
			background: url('../img/icon/line-2.png')
			background-size: contain !important
			background-position: bottom right !important
			height: r(100px)
			@media screen and ( max-width: 576px)
				background-position: bottom center !important
		a
			display: flex
			height: r(302px)
			width: auto
			@apply justify-center items-end relative z-30 ml-4
			img
				width: auto
				height: 100%
				object-fit: contain

\:root
	--swiper-image-ratio: 75%
	--swiper-width: 20%
	--scalingx: .8
	--scalingy: .8
.about-7
	.item
		@apply relative
	.txt
		transition: .3s all  ease-in-out
		@screen lg
			@apply opacity-0 w-full absolute-x top-full  transition
	.prev,.next
		@screen lg
			top: 36%
	.swiper
		@screen max-lg
			@apply mb-8
		@screen lg
			padding-bottom: 120px
		@screen 2xl
			@apply pb-25
	.large-center-swiper
		@apply relative
		@screen sm
			@apply  px-[20px] mt-10
		.img
			@apply w-full h-full
			a
				position: relative
				display: block
				height: 0
				overflow: hidden
				padding-top:  var(--swiper-image-ratio)
				img
					@apply absolute-center
					width: 100%
					height: 100%
					object-fit: contain
					transition: .3s ease-in-out all
		
	.swiper-slide
		width: var(--swiper-width)
		padding: 0 
		transition: .3s all  ease-in-out
		.img
			@apply opacity-30 transition
		.item
			transition: .3s all  ease-in-out
			.wrap
				transition: .3s all  ease-in-out
		.item
			transform: scale(var(--scalingx),var(--scalingy)) translateZ(0)
			.wrap
				.box
					transition: all 0.3s ease
					height: auto
					text-align: center
					@apply  overflow-hidden  
					img
						transform: scale(1,1) translate(-50%,-50%)
						@apply object-cover
		&.swiper-slide-active
			.txt
				@apply opacity-100
			.item
				.img
					@apply opacity-100
				transform: scale(1) translateZ(0)  !important
				.wrap
					transform: scale(1) !important
					img
						transform: scale(1) translate(-50%,-50%) !important
