.project-detail
	.row-order-detail
		@screen max-lg
			.col
				&:first-child
					@apply order-2
				&:nth-child(2)
					@apply order-1
	.product-thumb
		@screen md
			padding: 0 r(70px)
	.bg-wrap
		p
			@apply mb-0
	.prev,.next
		width: r(46px) !important
		min-width: r(46px) !important
		height: r(40px) !important
		@apply border border-grey-500
		&::before
			@apply text-grey-500 text-xs
			@screen xl
				@apply text-base
	.prev,.next
		@media screen  and ( min-width: 768px) and ( max-width: 1024px)
			@apply absolute-y
	.prev
		@apply left-0
		@screen max-lg
			@apply -left-3
	.next
		@apply right-0
		@screen max-lg
			@apply -right-3
	.swiper-slide-thumb-active
		.img
			@apply border-primary-red

	.img
		a
			display: flex
			height: r(125px)
			width: 100%
			img
				width: 100%
				height: 100%
				object-fit: cover
.project-other
	.project-item
		@apply bg-white
		&:hover
			@apply bg-primary-1
	.prev,.next
		width: r(46px) !important
		min-width: r(46px) !important
		height: r(40px) !important
		@apply border border-grey-500
		&::before
			@apply text-grey-500 text-xs
			@screen xl
				@apply text-base