.home-partner
	background:  var(--bg-url), #CEE3FF
	background-repeat: no-repeat
	background-position: center
	@screen xl
		background-size: 100% 100%
	@screen max-lg
		@apply pb-[150px] !#{important}
	.row
		@apply justify-between
	.img
		a
			height: r(128px)
			img
				width: auto
				height: 100%
				object-fit: contain
	.swiper-nav
		@apply justify-start 
		@screen max-lg
			@apply flex-center
	.nav-wrap
		@screen max-lg
			@apply absolute-x
			bottom: -40px
	.scrollbar-wrap
		max-height: 100%
.sliding-text-swiper
	@apply absolute-x  bottom-0 pb-5 w-screen
	.swiper-wrapper
		transition-timing-function: linear 
	.swiper-slide
		@apply h-auto w-fit pointer-events-none
	span
		font-size: r(72px)
		@screen xl
			font-size: r(96px)
		@screen 2xl
			font-size: r(128px)
	.txt
		@apply flex-start
