.home-number
	+left-icon(630px,580px,'../img/overlay/home-2.svg',unset,0,0)
	&::after
		content: ''
		@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
		background: linear-gradient(140deg, rgba(255, 255, 255, .5) 0%, rgba(255, 255, 255, 0) 100%)
		max-width: r(1296px)
	.img
		a
			max-height: r(780px)
			img
				object-fit: contain
	.counter
		sup
			top: -12px
		@media screen and ( max-width: 1600px)
			@apply text-4xl
	.scrollbar-wrap
		@screen sm
			max-height: r(140px)