header
	height: var(--header-height)
	@apply overflow-visible !#{important}
	&::before,&::after
		content: ''
		@apply block absolute pointer-events-none left-0 top-0 z-10  transition w-full h-full
	&::before
		background: linear-gradient(180deg, #FFFFFF 0%, rgba(255, 255, 255, 0) 100%) 
	&::after
		background: linear-gradient(180deg, #104891 0%, rgba(16, 72, 145, 0) 100%)
		@apply opacity-0
.header-social
	@apply pl-16
	em,i 
		@apply text-white text-xl
		@media screen and ( min-width: 1024px) and ( max-width: 1400px)
			@apply text-lg
	img
		@apply max-w-5
	a
		@apply bg-transparent flex-center transition overflow-hidden rounded-full w-[40px] h-[40px] min-w-[40px] border border-white
		@media screen and ( min-width: 1024px) and ( max-width: 1400px)
			@apply  w-[32px] h-[32px] min-w-[32px]
		@screen 2xl
			@apply  min-w-10 w-10 h-10
		&:hover
			transform: translateY(-4px)
main
	@screen max-xl
		padding-top: var(--header-height)
.txt-grid
	display: grid 
	grid-template-rows: 0fr
	transition: .4s grid-template-rows ease
	> div
		overflow: hidden

.header-btn-group
	@screen max-sm
		@apply absolute-x bottom-[80px] left-0 w-full  z-50 h-fit border-t border-white
		.btn
			@apply w-full rounded-none border border-primary-1
			&:hover
				@apply bg-white
				em,span,i
					@apply text-primary-1
.nav-brand
	a
		height: 50px
		width: auto
		@apply transition flex-center
		@media screen and ( min-width: 1200px)
			height: r(80px)
		@media screen and ( min-width: 1400px)
			@apply h-24
		img,svg
			width: 100%
			height: 100%
			object-fit: contain
.mobile-nav-wrap
	@apply fixed top-0 left-0 w-full h-full bg-primary-1 flex flex-col justify-center items-end
	z-index: 70
	padding: r(30px)
	width: 320px
	transform: translateX(-100%)
	transition: transform .8s cubic-bezier(.165,.84,.44,1)
	@screen max-sm
		@apply w-full
	@media screen and ( min-width:1200px )
		width: r(600px)
		padding: r(40px)
	.block-wrap
		@screen max-xl
			@apply w-full
		@screen xl
			padding-right: r(30px)
	&.is-open
		transform: none
	&::after
		content: ''
		@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full opacity-10
		width: r(370px)
		height: r(280px)
		background: url('../img/overlay/menu-line.png')
		background-repeat: no-repeat
		background-size: contain
		background-position: center left
		@screen max-xl
			width: r(200px)
			height: r(150px)
	.scrollbar-wrap
		max-height: r(550px)
		@apply overflow-x-hidden