.home-project-sample
	@screen max-xl
		@apply py-0  !#{important}
	.swiper-pagination
		@media screen and ( max-width: 1200px)
			@apply absolute-x bottom-4 !#{important}
	.heading-1
		@apply flex flex-col
		span
			@apply font-normal text-xl
	.btn-wrap
		.btn
			@apply pointer-events-auto 
.flex-card-section
	.container-fluid
		@apply p-0
	.img-wrap
		a
			@screen xl
				@apply flex-center  h-screen
				width: auto
				img
					width: 100%
					height: 100%
					object-fit: cover
			@media screen and ( max-width: 1200px)
				+img-ratio(960/1008)
	.headline
		@apply whitespace-nowrap !#{important}
		@apply capitalize
		span
			@apply block font-light 
	.item
		@apply relative
		height: inherit
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10   w-full h-full
			transition: .4s all  ease-in-out
			background: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)),linear-gradient(270deg, rgba(0, 0, 0, 0.4) 0%, rgba(0, 0, 0, 0) 100%)
	.txt
		@apply absolute-center w-full h-full   pointer-events-none z-30 flex p-5
		align-items: flex-start 
		transition: .4s all  ease-in-out
		@screen xl
			padding: 0 0 r(4px) r(50px)
		@screen max-xl
			@apply pb-15
		>.headline
			transition: .5s all  ease-in-out
			transform-origin: 0
			@screen xl
				transform: rotate(-90deg)
	.desc
		margin: r(20px) 0
	
	.flex-options
		display: flex
		flex-direction: row
		align-items: stretch
		overflow: hidden
		@apply w-full h-full
		@media screen and ( max-width: 1200px)
			@apply hidden
	.card-option
		transform: scale(1)
		background-size: cover
		position: relative
		width: 12.81%
		overflow: hidden
		cursor: pointer
		transition: .6s all ease-in-out
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full opacity-0
			background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 33.18%, rgba(#000, .5) 100%)
		&:not(.active)
			flex-grow: 1
			.heading-1
				@apply text-2xl font-bold
			.headline
				text-transform: none
		&.active
			flex-grow: 10000
			transform: scale(1)
			width: 61.5%
			&::after
				@apply opacity-100
			.heading-1
				@apply font-extrabold
			.container
				@apply opacity-100
			.sub-title
				text-transform: none
			.txt
				padding: 0 0 r(60px) r(50px)
				@apply items-start
				.headline,.sub-title
					writing-mode: unset !important
					transform: rotate(0)
					@apply whitespace-nowrap
				.headline
					@apply  uppercase
			.txt-wrap
				height: 140px
				@apply w-full
		.card-label
			@apply w-full h-full
			transition: 0.5s cubic-bezier(0.05, 0.61, 0.41, 0.95)

	.txt-wrap
		@apply h-0  overflow-hidden
		transition: .4s all  ease-in-out
		.desc
			+line(2)
	.two-swiper
		@screen xl
			@apply hidden
		@media screen and ( max-width: 1200px)
			@apply block