
.project-list

	.block-swiper
		.item
			@apply my-0  bg-grey-50
			.col
				@apply my-0
	.item
		@apply h-full
		@screen max-lg
			@apply flex flex-col
		.list
			li
				&:not(:last-child)
					@apply mb-4
			.icon
				em,i
					@apply text-xl text-primary-1 
		.heading-2
			@media screen and ( max-width: 1400px)
				@apply line-clamp-3
		.col
			@screen max-lg
				&:nth-child(1)
					@apply order-2
					flex: 0 1  auto
					@apply h-auto
				&:nth-child(2)
					@apply order-1
					
					flex: 0 0 auto
					@apply w-full 
	
	.scrollbar-wrap
		max-height: r(180px)
		@screen max-sm
			max-height: 100%
.zone-nav
	li
		a
			@apply text-base
		&.active
			a
				@apply bg-primary-2 text-white
	ul
		@media screen and ( min-width: 1800px)
			@apply flex-center
	+res-nav
.news-list-nav
	@media screen and ( min-width: 1400px)
		ul
			@apply justify-center 
.term-du-an-thi-cong 
	.zone-nav
		@media screen and ( min-width: 1600px)
			ul
				@apply justify-center 
.term-du-an-dau-tu,.term-32,.term-33,.term-31
	.zone-nav
		@media screen and ( min-width: 768px)
			ul
				@apply justify-center 
.zone-nav
	overflow-x: auto
.zone-nav::-webkit-scrollbar
	background: #D9D9D9
	height: 4px !important
	border-radius: 4px
.zone-nav::-webkit-scrollbar-track
	-webkit-box-shadow: inset 0 0 4px #D9D9D9
	background-color: #D9D9D9
	border-radius: 4px
.zone-nav::-webkit-scrollbar-thumb
	background-color: #104891
@-moz-document url-prefix()
	.zone-nav
		scrollbar-width: thin
		scrollbar-color: #12171d #D9D9D9
.category-to-chuc-chinh-tri-xa-hoi,.category-27
	.zone-nav
		ul
			@apply pb-0
			@apply justify-center !#{important}
.sticky-nav
	top: 0 !important
	&.scroll-to-fixed-fixed
		top:  var(--header-height) !important
	&.reached-limit
		top: unset !important
		position: static !important
.project-list
	@screen max-lg
		
		.modulepager
			@apply pt-20
.project-item
	@apply h-full
	.icon
		em,i
			@apply text-base text-primary-1 font-light
	.list
		li
			&:not(:last-child)
				@apply mb-4
	&:hover
		@apply bg-primary-1
		.heading-2
			@apply text-white
		.icon
			img
				filter: brightness(0) invert(1)
			em,i
				@apply text-white
		.body-3
			@apply text-white