.home-recruit
	background-position: center bottom !important
	@screen xl
		@apply pb-11 justify-end !#{important}
	.col
		@screen lg
			&:nth-child(1)
				width: 65%
			&:nth-child(2)
				width: 35%
	.scrollbar-wrap
		max-height: r(511px)
.recruit-news
	@apply transition 
	@screen sm
		@apply flex-start 
	.img
		@apply m-0
		flex: 1 0 auto
		a
			@apply overflow-hidden  flex-center
			img
				width: 100%
				height: 100%
				object-fit: cover
			@screen max-sm
				+img-ratio(167,297)
			@screen sm
				width: r(297px)
				min-width: r(297px)
				max-width: r(297px)
				height: r(167px)
	.txt
		flex: 0 1 auto
		
	&:not(:last-child)
		@apply mb-4
