//Type 1 first child icon
.modulepager,.post-nav
	@apply flex items-center justify-center pt-10
	ul
		padding: 0
		margin: 0
		position: relative
		@apply flex-center gap-6
		li
			display: inline-block
		
			
			span,a
				text-decoration: none
				transition: .3s all  ease-in-out
				@apply text-lg font-bold  flex-center overflow-hidden text-primary-1 border border-primary-1 rounded-full 
				width: 36px
				min-width: 36px
				height: 36px
				@screen xl
					@apply min-w-12  w-12 h-12 
				&.active,&:hover,&.current
					@apply bg-primary-1 text-white 
				
		.next
			a
				@apply whitespace-nowrap
					
