.page-template-template_about
	.rank-math-breadcrumb
		p
			a
				@apply  text-grey-300
				&:first-child
					&:before
						@apply  text-grey-300

.rank-math-breadcrumb
	min-height: 58px
	@apply  x-start h-full py-2
	p
		@apply  flex font-normal mb-0  x-start flex-wrap relative  text-base text-grey-500
		justify-content: flex-start
		align-items: center
		*
			@apply  font-normal
		span 
			@apply whitespace-nowrap  text-base
			*
				@apply  text-base
		.separator
			margin: 0 
			padding: 0 20px
			font-size: 0
			width: auto
			@apply relative
			@media screen and ( max-width: 576px)
				@apply mx-0
			&:first-child
				margin-left: 24px !important
			&:after
				content: ''
				@apply  block font-normal pointer-events-none z-10   bg-grey-500
				height: 18px
				width: 1px
				@media screen and ( max-width: 1024px)
					font-size: 14px
				@media screen and ( max-width: 576px)
					@apply left-[-6px]
		.last
			margin-top: 3px
			@apply  whitespace-normal font-bold text-green
		a
			@apply whitespace-nowrap  text-base relative text-grey-500
			@media screen and ( max-width: 576px)
				@apply  mt-1 
			+ .separator
				margin: 0
			&:first-child
				width: 24px
				height: 28px
				@apply x-center
				font-size: 0 !important
				margin-top: 0 !important
				&:before
					content: '\f015'
					font-family: 'Font Awesome 6 Pro'
					@apply  block pointer-events-none z-10 font-black text-xl text-grey-500


