
.culture-detail
	.container-fluid
		@apply p-0
	.row
		@apply justify-between
	time
		@apply px-0 text-base text-grey-500
		&:before
			@apply hidden
	
	#ez-toc-container
		ul
			li
				&::before
					@apply hidden
	.scrollbar-wrap
		max-height: r(200px)
	.block-wrap
		&::before
			content: ''
			@apply block absolute pointer-events-none  top-0 z-10  transition w-full h-[1px] bg-primary-1 rem:max-w-[200px]
			right: calc( 100% + 40px )
.culture-other
	.news-item
		@apply bg-white
	.prev,.next
		width: r(46px) !important
		min-width: r(46px) !important
		height: r(40px) !important
		@apply border border-grey-500
		&::before
			@apply text-grey-500 text-xs
			@screen xl
				@apply text-base