.recruit-page
	.modulepager
		@apply hidden
.recruit-1
	.row
		@apply m-0 p-0
		.col
			@apply m-0 p-0
	.fullcontent
		h4,h5,h6
			@apply text-primary-1
	.container-fluid
		@apply p-0
		&:nth-child(odd)
			@apply bg-white
			.row
				@apply flex-row-reverse
			.txt
				@screen lg
					@apply ml-auto pl-0 pr-10
				@screen xl
					@apply pr-20
	.txt
		@screen max-lg
			@apply px-10 py-15
	.scrollbar-wrap
		max-height: r(270px)
		@apply pr-1
	.img
		a
			@screen max-lg
				+img-ratio(610,960)
			@screen lg
				height: r(610px)
				@apply w-full
				img
					width: 100%
					height: 100%
					object-fit: cover
.recruit-2
	.desc
		@apply text-base line-clamp-3
		*
			@apply text-base line-clamp-3
		&.desc-expanded
			height: 100%
			-webkit-line-clamp: unset
			*
				height: 100%
				-webkit-line-clamp: unset
	.heading-2
		+line-clamp(20px,24px,1.35,2)
	.btn
		span
			@apply font-normal
		em,i
			&::before
				content: "\f078"
.otgs-development-site-front-end
	@apply hidden !#{important}
.recruit-list
	.filter-table-wrap
		tbody
			tr
				@apply hidden
	.ajax-btn-wrap
		&.disble-btn
			@apply hidden
	
	.btn
		span
			@apply whitespace-nowrap
	table
		@apply overflow-hidden bg-white  border-collapse
		thead 
			th
				@apply  text-white bg-primary-1 uppercase font-bold text-lg px-5 border  py-4 text-center border-grey-200
				*
					@apply  text-white uppercase font-bold text-lg
				&:nth-child(1)
					@apply text-center
					width: 90px
					max-width: 90px
				&:nth-child(2)
					width: 42.5%
					@apply text-left
				&:nth-child(3),&:nth-child(4),&:nth-child(5)
					width: 17.4%
				&:nth-child(4)
					@screen xl
						@apply px-1
				@screen xl
					@apply text-2xl
					*
						@apply text-2xl
		tbody
			tr
				&:hover
					td:nth-child(1),.title
						@apply text-primary-1 font-bold
						*
							font: inherit
				td
					@apply px-5  text-center text-base  py-3 border border-grey-200 
					*
						@apply text-base
					&:nth-child(1)
						@apply text-center font-bold
						width: 90px
						max-width: 90px
					&:nth-child(2)
						width: 42.5%
						@apply text-left
					&:nth-child(3),&:nth-child(4),&:nth-child(5)
						width: 17.4%
					&:hover
						a
							@apply text-primary-1 
				
	.table-wrap
		@apply overflow-hidden 
		@media screen and ( max-width: 1024px)
			+table-res
	.btn-tertiary
		@apply w-full flex-center
		span
			@apply text-primary-1 text-sm
		em,i
			@apply  text-base text-primary-1 mx-0 mr-2
	.ajax-btn-wrap
		.btn.btn-primary
			span,em,i
				@apply text-lg font-normal
			span
				@apply normal-case !#{important}
			&:hover
			
				span
					@apply text-white
			