\:root
	--image-ratio: 142%
	--swiper-width: 20%
	--scalingx: .8
	--scalingy: .8
.ability-7
	@apply relative z-50
	margin-top: -1px
	&::after
		content: '' 
		@apply block absolute pointer-events-none left-0 top-0 z-10  transition w-full h-full
		background: url('../img/bg/ability-3.png')
		background-repeat: no-repeat
		background-size: contain
		background-position: center top
		@media screen and ( max-width: 1024px)
			background-size: cover
			@apply h-1/2
	
	.block-wrap
		@screen lg
			padding: 0 r(100px)

	.body-1,.headline 
		@apply line-clamp-1
		*
			@apply line-clamp-1
	.prev
		@apply left-0 
		@screen lg
			top: 38%
		@screen 2xl
			top: 40%
	.next
		@apply right-0
		@screen lg
			top: 38%
		@screen 2xl
			top: 40%
	.swiper-nav
		@screen max-lg
			@apply mt-10
	.swiper-button-disabled-custom
		@apply pointer-events-none
	.item
		@apply relative
	.txt
		transition: .3s all  ease-in-out
		@screen lg
			@apply opacity-0 w-full absolute-x top-full  transition
	.prev,.next
		@screen lg
			top: 36%
	.swiper
		@screen max-lg
			@apply mb-8
		@screen lg
			padding-bottom: 120px
		@screen 2xl
			@apply pb-25

	.large-center-swiper
		@apply relative
		@screen sm
			@apply  px-[20px] mt-10
		.img
			@apply w-full h-full
			a
				position: relative
				display: block
				height: 0
				overflow: hidden
				padding-top:  var(--image-ratio)
				img
					@apply absolute-center
					width: 100%
					height: 100%
					object-fit: contain
					transition: .3s ease-in-out all
		
	.swiper-slide
		width: var(--swiper-width)
		padding: 0 
		transition: .3s all  ease-in-out
		.img
			@apply  transition bg-white
			img 
				@apply  opacity-50
		

		.item
			transition: .3s all  ease-in-out
			.wrap
				transition: .3s all  ease-in-out
		.item
			transform: scale(var(--scalingx),var(--scalingy)) translateZ(0)
			.wrap
				.box
					transition: all 0.3s ease
					height: auto
					text-align: center
					@apply  overflow-hidden  
					img
						transform: scale(1,1) translate(-50%,-50%)
						@apply object-cover
		&.swiper-slide-active
			.txt
				@apply opacity-100
			.item
				.img
					@apply opacity-100
				transform: scale(1) translateZ(0)  !important
				.wrap
					transform: scale(1) !important
					img
						transform: scale(1) translate(-50%,-50%) !important
