.home-field
	+right-icon(675px,636px,'../img/overlay/home-4.png',unset,0,0)
	@screen xl
		justify-content: flex-end !important
		padding-bottom: r(100px) !important
	.item
		.text 
			@screen max-sm
				@apply mt-0
.flex-card-section-2
	.img-wrap
		a
			@screen lg
				@apply flex-center transition
				height: r(550px)
				width: auto
				img
					width: 100%
					height: 100%
					object-fit: cover
			@media screen and ( max-width: 1024px)
				+img-ratio(342/680)
	.item
		@apply relative
		height: inherit
	.txt
		@apply absolute-center w-full h-full  bg-opacity-80 pointer-events-none z-30 bg-primary-1 
		transition: .4s all  ease-in-out
		>.heading-5
			transition: .5s all  ease-in-out
		
	.text
		@apply  z-30 opacity-0 p-8 w-full transition  relative h-full
		@media screen and ( max-width: 1024px)
			@apply opacity-100
		.headline
			transition: .5s all  ease-in-out
	.flex-options
		display: flex
		flex-direction: row
		align-items: stretch
		overflow: hidden
		@apply w-full h-full
		@media screen and ( max-width: 1024px)
			@apply hidden
	.card-option
		transform: scale(1)
		background-size: cover
		position: relative
		width: 12.5%
		overflow: hidden
		cursor: pointer
		transition: .6s all ease-in-out
		border: 10px solid transparent
		height: r(550px)
		&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full opacity-0
			background: linear-gradient(180deg, rgba(0, 0, 0, 0.00) 33.18%, rgba(#000, .5) 100%)
		&:not(.active)
			flex-grow: 1
		&.active
			flex-grow: 10000
			transform: scale(1)
			width: 50%
			.card-label
				box-shadow: 0px 0px 16px 0px #0000001A
			.img-wrap
				a
					@apply flex-center w-full
					height: r(342px)
			.text
				@apply opacity-100
			.txt
				@apply opacity-0
			.txt-wrap
				height: 140px
				@apply w-full
		.card-label
			@apply w-full h-full
			transition: 0.5s cubic-bezier(0.05, 0.61, 0.41, 0.95)
	.two-swiper
		@screen xl
			@apply hidden
		@media screen and ( max-width: 1024px)
			@apply block
	.desc
		+line-clamp(16px,18px,1.4,3)