
.primary-banner
	@apply relative z-10 overflow-hidden
	@media screen and ( max-width: 1200px)
		padding: 0 !important
	.wrap
		@apply relative
		@media screen and ( max-width: 1024px)
			@apply static 
	.pagination-wrap
		@screen max-lg
			@apply justify-center pb-8 !#{important}
	.container
		padding-bottom:  var(--banner-padding)
	.swiper-slide
		@apply h-auto pointer-events-none 
		z-index: -1
		&.swiper-slide-active
			z-index: 999
			@apply pointer-events-auto
	.img
		@apply relative  z-20
		&::before
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
			background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, #000000 100%)
			max-height: r(675px)
		@screen max-md
			a
				+aspect-ratio(635,320)
		@screen md
			a
				@apply flex-center w-full h-full
				img,picture,video
					width: 100%
					height: 100vh
					object-fit: cover
	
	.video
		a
			&.pause
				&::after
					@apply opacity-100	
			&::after
				content: '\f04b'
				@apply block  pointer-events-none opacity-0 z-50  transition w-full  absolute-center   font-Awesome6 border border-white rounded-full overflow-hidden text-4xl leading-3 flex-center text-opacity-100 text-white 
				background-repeat: no-repeat
				background-size: cover
				background-position: center
				width: r(100px)
				height: r(100px)
				min-width: r(100px)
				@media screen and ( max-width: 1024px)
					width: r(64px)
					height: r(64px)
					min-width: r(64px)
					@apply text-2xl
	.desktop
		@apply hidden
		@screen md
			@apply block 
	.mobile
		@apply block
		@screen md
			@apply hidden 
	.paginaton-wrap
		padding-bottom:  var(--banner-padding)
	.swiper-pagination
		@apply m-0 justify-end !#{important}
	.prev,.next
		@apply bg-dark bg-opacity-20 border border-white
		&::before
			@apply z-60 text-white text-opacity-100
	.swiper-pagination
	
		.swiper-pagination-bullet 
			@apply relative   transition  rounded-none w-[20px]  min-w-[20px]  h-[3px]
			@apply bg-white opacity-50 !#{important}
			border: none !important
			@screen lg
				@apply rem:w-[30px] rem:min-w-[30px] rem:h-[4px]
			&::before,&::after
				@apply hidden !#{important}
			&.swiper-pagination-bullet-active
				@apply opacity-100 !#{important}
				@screen lg
					@apply rem:w-[76px] rem:min-w-[76px]
				
			