.home-about
	+left-icon(500px,445px,'../img/overlay/home-1.png',unset,0,0)
	.block-wrap-center
		@apply  h-full relative
	.scrollbar-wrap
		@media screen and ( min-width: 1200px) and ( max-width: 1600px)
			max-height: r(340px)
	.swiper
		@apply h-full m-0 p-0
		.swiper-slide
			@apply p-0 
	.txt-wrap
		@screen xl
			padding-right: r(160px)
	.img-wrap
		@apply h-full
		&::before,&::after
			content: ''
			@apply block absolute pointer-events-none left-0 bottom-0 z-10  transition w-full h-full
		&::before
			@apply opacity-0
			height: r(362px)
			background: linear-gradient(180deg, rgba(0, 179, 92, 0) 0%, #00914A 93%)
		&::after
			background: linear-gradient(180deg, rgba(0, 0, 0, 0) 44.27%, rgba(0, 0, 0, 0.75) 100%)
			height: r(260px)
		@media screen and ( min-width: 576px) and ( max-width: 1200px)
			height: 50vh
		@media screen and ( max-width: 1200px)
			a
				+img-ratio(2/1)
		@media screen and ( max-width: 576px)
			a
				+img-ratio(1/1)
		a
			@apply flex-center h-full w-full
			img
				@apply h-full w-full
				object-fit: cover
	
	.item
		@apply relative h-full
		margin: .5px
		&:hover
			.img-wrap
				&::before
					@apply opacity-100
				&::after
					@apply opacity-0
	.icon
		@apply flex-center mb-4
		height: r(60px)
		width: r(60px)
		@screen xl
			@apply mb-5
		img
			height: r(60px)
			width: r(60px)
	.txt
		@apply absolute left-0 bottom-0 pointer-events-none z-30 p-6 w-full
		padding-top: r(100px)
	.wrap-left
		@media screen and ( max-width: 1200px)
			@apply pb-6
	.headline
		span
			@apply block
			+ span
				@apply mt-1